.marker {
  transition: all 7s ease-in-out;
  border-radius: 15px!important;
  border: 1px solid rgb(192, 138, 138);
  display: inline-block; 
  width: 30px!important;
  text-align: center;
  height: 30px!important;
  line-height: 30px;
  color: #FFF!important;
  font-weight: bold;
}
 
.bus {
  background-color: #333;
}

.rail90 {
  background-color: #D11241;
}
.rail100 {
  background-color: #0069AA;
}
.rail190 {
  background-color: #FFC423;
}
.rail200 {
  background-color: #008752;
}
.rail290 {
  background-color: #D25D13;
}
 
 /* Style for Leaflet zoom controls */
.leaflet-control-zoom .leaflet-bar a {
  background-color: rgba(255, 255, 255, 0.7) !important; /* Semi-transparent white */
  border-bottom: 1px solid rgba(204, 204, 204, 0.7) !important;
}
.leaflet-control-zoom .leaflet-bar {
  border: none !important;
}
